import GetModelImageData from '../utils/GetModelImageData';
import Button from 'react-bootstrap/Button';
import { useEffect } from "react";
import Tooltip from 'react-qtip';

const SurveyPart2 = () => {
    var userData = undefined;

    useEffect(() => {
        if(sessionStorage.getItem('rankData') == null) {
            userData = ["","",""];
            window.sessionStorage.setItem('rankData', JSON.stringify(userData));
        }
        else {
            userData = JSON.parse(window.sessionStorage.getItem('rankData'));
        }
        setSelectData();       
      },[])

    function Next() {
        saveData();
        document.getElementById("SurveyPart2").style.display = "none";
        document.getElementById("SurveyPart3").style.display = "inline";       
    }

    function Previous() {
        saveData();
        document.getElementById("SurveyPart1").style.display = "inline";
        document.getElementById("SurveyPart2").style.display = "none";
    }

    function setSelectData() {
        if(userData[0] != "") {
            document.getElementById("rank-one").value = userData[0];
        }
        if(userData[0] != "") {
            document.getElementById("rank-two").value =  userData[1];
        }
        if(userData[0] != "") {
            document.getElementById("rank-three").value =  userData[2];
        } 
    }

    function saveData() {
        userData[0] = document.getElementById("rank-one").value;
        userData[1] = document.getElementById("rank-two").value;
        userData[2] = document.getElementById("rank-three").value;
        window.sessionStorage.setItem('rankData', JSON.stringify(userData)); 
    }

    return (
        <div id="SurveyPart2">
            <div>
                <h1>Del 2</h1>
            <br>
            </br>
            <br>
            </br>
            </div>
            <div className="row">
                <div style={{float: "left", width: "50%"}}>
                    <div id="A" className="ModelDiv">
                    <h2 style={{display: "inline"}}>A</h2>     
                    <img src={GetModelImageData(0)} width="135" height="135" style={{marginLeft: "10px"}}></img>
                    <img src={GetModelImageData(1)} width="135" height="135" style={{marginLeft: "10px"}}></img>
                    <img src={GetModelImageData(2)} width="135" height="135" style={{marginLeft: "10px"}}></img>
                    <div>
                    <img src={GetModelImageData(0)} width="250" height="250" style={{marginRight: "10px"}}></img>
                    <img src={GetModelImageData(1)} width="250" height="250" style={{marginRight: "10px"}}></img>
                    <img src={GetModelImageData(2)} width="250" height="250"></img>
                    </div>         
                    </div>
                    <br>
                    </br>
                    <div id="B" className="ModelDiv">
                    <h2 style={{display: "inline"}}>B</h2>    
                    <img src={GetModelImageData(3)} width="135" height="135" style={{marginLeft: "10px"}}></img>
                    <img src={GetModelImageData(4)} width="135" height="135" style={{marginLeft: "10px"}}></img>
                    <img src={GetModelImageData(5)} width="135" height="135" style={{marginLeft: "10px"}}></img>
                    <div>
                    <img src={GetModelImageData(3)} width="250" height="250" style={{marginRight: "10px"}}></img>
                    <img src={GetModelImageData(4)} width="250" height="250" style={{marginRight: "10px"}}></img>
                    <img src={GetModelImageData(5)} width="250" height="250"></img>
                    </div>                           
                    </div>
                    <br>
                    </br>
                    <div id="C" className="ModelDiv">
                    <h2 style={{display: "inline"}}>C</h2>    
                    <img src={GetModelImageData(6)} width="135" height="135" style={{marginLeft: "10px"}}></img>
                    <img src={GetModelImageData(7)} width="135" height="135" style={{marginLeft: "10px"}}></img>
                    <img src={GetModelImageData(8)} width="135" height="135" style={{marginLeft: "10px"}}></img>
                    <div>
                    <img src={GetModelImageData(6)} width="250" height="250" style={{marginRight: "10px"}}></img>
                    <img src={GetModelImageData(7)} width="250" height="250" style={{marginRight: "10px"}}></img>
                    <img src={GetModelImageData(8)} width="250" height="250"></img>
                    </div>   
                    <br>
                    </br>
                    <br>
                    </br>            
                    </div>
                    <div>
                        <i>Hll musenpekaren ver en rad fr att frstora bilderna</i>
                    </div>
                </div>
                <div style={{float: "left", width: "50%"}}>
                    <p style={{textAlign: "center"}}>Varje bokstav motsvarar en typ av frklaring. Rangordna frklaringarna frn ett till tre baserat p vilken frklaring <br></br>som du knner <b>mest tillit</b> till.</p>
                    <div style={{textAlign: "center"}}>
                        <div style={{marginBottom: "30px", textAlign: "center"}}>
                            <h4 style={{display: "inline", marginRight: "20px"}}>1.</h4>
                            <select id="rank-one" style={{width: "150px"}} onChange={ () => {
                                if(document.getElementById("rank-two").value == document.getElementById("rank-one").value) {
                                    document.getElementById("rank-two").selectedIndex = 0;
                                }
                                if(document.getElementById("rank-three").value == document.getElementById("rank-one").value) {
                                    document.getElementById("rank-three").selectedIndex = 0;
                                }                                 
                            }}> 
                                <option value="" hidden>Vlj en frklaring</option>
                                <option value="A" style={{textAlign: "left"}}>A</option>
                                <option value="B" style={{textAlign: "left"}}>B</option>
                                <option value="C" style={{textAlign: "left"}}>C</option>     
                            </select>
                        </div>
                        <div style={{marginBottom: "30px", textAlign: "center"}}>
                        <h4 style={{display: "inline", marginRight: "20px"}}>2.</h4>
                            <select id="rank-two" style={{width: "150px"}} onChange={ () => {
                                 if(document.getElementById("rank-two").value == document.getElementById("rank-one").value) {
                                    document.getElementById("rank-one").selectedIndex = 0;
                                }
                                if(document.getElementById("rank-two").value == document.getElementById("rank-three").value) {
                                    document.getElementById("rank-three").selectedIndex = 0;
                                }       
                        }}>
                                <option value="" hidden>Vlj en frklaring</option>
                                <option value="A" style={{textAlign: "left"}}>A</option>
                                <option value="B" style={{textAlign: "left"}}>B</option>
                                <option value="C" style={{textAlign: "left"}}>C</option> 
                            </select>
                        </div>
                        <div style={{marginBottom: "30px", textAlign: "center"}}>
                        <h4 style={{display: "inline", marginRight: "20px"}}>3.</h4>
                        <select id="rank-three" style={{width: "150px"}} onChange={ () => {
                                 if(document.getElementById("rank-three").value == document.getElementById("rank-one").value) {
                                    document.getElementById("rank-one").selectedIndex = 0;
                                }
                                if(document.getElementById("rank-three").value == document.getElementById("rank-two").value) {
                                    document.getElementById("rank-two").selectedIndex = 0;
                                }       
                        }}>
                                <option value="" hidden>Vlj en frklaring</option>
                                <option value="A" style={{textAlign: "left"}}>A</option>
                                <option value="B" style={{textAlign: "left"}}>B</option>
                                <option value="C" style={{textAlign: "left"}}>C</option>  
                            </select>  
                        </div>
                    </div>
                    <div style={{textAlign: "center"}}>
                        <br>
                        </br>
                        <br>
                        </br>
                        <Button id="Previous-button" style={{ marginBottom: "20px", marginRight: "20px", width: "80px"}} variant="outline-secondary" onClick={Previous}>Tillbaka</Button>
                        <Button id="Next-button" style={{ marginBottom: "20px", marginLeft: "20px", width: "80px"}} variant="outline-secondary"onClick={Next}>Nsta</Button>       
                    </div>
                </div>
            </div>                         
        </div>
    )
}

export default SurveyPart2;