import CamelOcc from '../images/Occlusion/CamelOcclusion.png';
import ToucanOcc from '../images/Occlusion/ToucanOcclusion.png';
import GorillaOcc from '../images/Occlusion/GorillaOcclusion.png';
import CamelLay from '../images/LayerGradCam/CamelLGC.png'; //Need to decide on which layer to use
import ToucanLay from '../images/LayerGradCam/ToucanLGC.png'; //Need to decide on which layer to use
import GorillaLay from '../images/LayerGradCam/GorillaLGC.png'; //Need to decide on which layer to use
import CamelHuman from '../images/resized/Camel.jpg'; //Placeholder
import ToucanHuman from '../images/resized/Toucan.jpg'; //Placeholder
import GorillaHuman from '../images/resized/Gorilla.jpg'; //Placeholder

const GetModelImageData = (index) => {
    var modelImageData = [
        [CamelOcc],
        [ToucanOcc],
        [GorillaOcc],
        [CamelLay],
        [ToucanLay],
        [GorillaLay],
        [CamelHuman],
        [ToucanHuman],
        [GorillaHuman],
    ];
    return modelImageData[index];
}
export default GetModelImageData;