import React from 'react';
import Navbar from '../../components/Navbar';
import '../../css/App.css';
import SurveyPart1 from '../../components/SurveyPart1';
import SurveyPart2 from '../../components/SurveyPart2';
import SurveyPart3 from '../../components/SurveyPart3';
import SurveyPart4 from '../../components/SurveyPart4';
import { useEffect } from "react";


const Survey = () => {
    useEffect(() => {
        document.getElementById("SurveyPart1").style.display = "inline";
        document.getElementById("SurveyPart2").style.display = "none";
        document.getElementById("SurveyPart3").style.display = "none";
        document.getElementById("SurveyPart4").style.display = "none";
      },[])
return (
    <div className="survey">
        <Navbar/>
        <div style={{height: "40vw", position: "relative"}}>
            <div style={{position: "absolute", top: "50%", left: "50%", transform: "translate(-50%, -50%)", textAlign: "center"}}>
                <SurveyPart1/>
                <SurveyPart2/>
                <SurveyPart3/>
                <SurveyPart4/>
            </div> 
        </div>          
    </div>
    )   
}



export default Survey;