import Button from 'react-bootstrap/Button';
import { useNavigate } from 'react-router-dom';
import { auth, provider} from '../../configs/firebaseConfig';
import { useEffect } from "react";
import '../../css/App.css';

const Login = () => {
  let navigate = useNavigate();

  useEffect(() => { 
    document.getElementById('Login-button').disabled = true;
  },[])

  async function signInWithGoogle() {
    auth
      .signInWithPopup(provider)
      .then((res) => {
      ;
          navigate("/home");
      })
      .catch((error) => {
        console.log(error);
      });

    }
  return (
    <div className="login">
      <div style={{height: "40vw", position: "relative"}}>
        <div style={{position: "absolute", top: "50%", left: "50%", transform: "translate(-50%, -50%)", transform: "translate(-50%, -50%)"}}>
          <div>
            <h1>Vlkommen till denna enkt om Explainable AI</h1>
            <br></br>
            <p>Du behver inte ha ngra frkunskaper inom AI fr att utfra enkten. Enkten syftar till att utforska vilka likheter och skillnader som kan ses mellan mnsklig objektklassificering och Explainable AI-metoder.</p>
            <p> Explainable AI r ett omrde med fokus p att skapa mnsklig frstelse fr hur en AI-modell fungerar. Mlet r att ge insikt i vad som pverkade en AI:s beslut och hur det pverkades. Detta grs genom algoritmer som, efter att den AI modellen r frdigtrnad, 
              retroaktivt frsker lista ut hur det fattar sina beslut. Mnga metoder fr detta finns, men de flesta baseras p att man p olika stt manipulerar delar av in-datan och observerar hur det frndrar beslutet.</p>
            <br></br>
            <p>Vrt ml r att se likheter och skillnader i vad dessa Explainable AI-algoritmer fr fram som de viktigaste aspekterna i objekt-klassificering hos en eller flera AI-modeller och jmfra detta med vad mnniskor prioriterar nr de presenteras med samma klassificeringsproblem. 
              P detta stt kan vi hitta eventuella mnster som kan antyda vilka Explainable AI-metoder som r mest lika mnskligt resonerande.</p>
            <p> Utifrn detta undersker vi om just dessa metoder ocks r mer givande fr att ka mnniskors frstelse fr en trnad AI-modell.</p>
            <br>
            </br>
            <h6>Den enda data som sparas r dina enktsvar, vilka r anonyma.
              <br>
              </br>
              Enkten tar ca <u>X</u> minuter att genomfra.
              <br>
              </br>
              <br>
              </br>
              Stort tack fr din tid.
            </h6>
            <br>
            </br>
            <input type="checkbox" id="checkbox" onClick={ () => {
              if(document.getElementById('checkbox').checked) {
                document.getElementById('Login-button').disabled = false;
              }
              else {
                document.getElementById('Login-button').disabled = true;
              }
            }}
            >            
            </input>
            <label>
              <i>
              Jag har tagit del av informationen ovan och samtycker till att delta. Jag r	medveten om att mitt deltagande r helt frivilligt och att	jag kan avbryta mitt deltagande i studien utan att ange ngot skl.
              </i>
              </label>
            <br>
            </br>
          </div>
          <Button id="Login-button" style={{ marginTop: "20px"}} variant="outline-secondary" onClick={ () => {
            signInWithGoogle();
          }}
          >
            Logga in med Google
          </Button>
        </div>
      </div>
    </div>
  )
}

export default Login;